"use client"

import { useEffect, useRef, useState } from "react"
import { Plus, Minus, ChevronDown } from "lucide-react"
import type { WaterObject } from "@/lib/types"

interface MapViewProps {
  objects: WaterObject[]
  selectedId?: string
  hoveredId?: string | null
  onSelect: (object: WaterObject) => void
  onHover: (id: string | null) => void
  onMapClick?: () => void
}

const tileLayers = {
  standard: {
    url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
    attribution: "© OpenStreetMap contributors",
  },
  satellite: {
    url: "https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}",
    attribution: "Tiles © Esri — Sources: Esri, i-cubed, USDA, USGS, AEX, GeoEye",
  },
  terrain: {
    url: "https://{s}.tile.opentopomap.org/{z}/{x}/{y}.png",
    attribution: "© OpenTopoMap contributors",
  },
} as const

type TileLayerKey = keyof typeof tileLayers

export function MapView({ objects, selectedId, hoveredId, onSelect, onHover, onMapClick }: MapViewProps) {
  const [mapType, setMapType] = useState<TileLayerKey>("standard")
  const [showMapMenu, setShowMapMenu] = useState(false)
  const mapContainerRef = useRef<HTMLDivElement | null>(null)
  const mapRef = useRef<any>(null)
  const tileLayerRef = useRef<any>(null)
  const markersRef = useRef<Record<string, any>>({})
  const initIntervalRef = useRef<NodeJS.Timeout | null>(null)

  const initializeMap = () => {
    if (typeof window === "undefined" || !(window as any).L || mapRef.current || !mapContainerRef.current) return

    const L = (window as any).L
    mapRef.current = L.map(mapContainerRef.current, {
      center: [46.0, 76.0],
      zoom: 6,
      zoomControl: false,
    })

    ;(window as any)._leafletMap = mapRef.current
    window.dispatchEvent(new Event("leaflet-map-ready"))
  }

  useEffect(() => {
    initializeMap()

    if (!mapRef.current) {
      initIntervalRef.current = setInterval(() => {
        if (mapRef.current) {
          if (initIntervalRef.current) clearInterval(initIntervalRef.current)
        } else {
          initializeMap()
        }
      }, 150)
    }

    return () => {
      if (initIntervalRef.current) clearInterval(initIntervalRef.current)
      if (mapRef.current) {
        mapRef.current.remove()
        if ((window as any)._leafletMap === mapRef.current) {
          ;(window as any)._leafletMap = null
        }
        mapRef.current = null
      }
    }
  }, [])

  useEffect(() => {
    if (!mapRef.current || !(window as any).L) return
    const L = (window as any).L
    const config = tileLayers[mapType]

    if (tileLayerRef.current) {
      tileLayerRef.current.remove()
    }

    tileLayerRef.current = L.tileLayer(config.url, {
      attribution: config.attribution,
      maxZoom: 19,
    })
    tileLayerRef.current.addTo(mapRef.current)
  }, [mapType])

  useEffect(() => {
    if (!mapRef.current || !(window as any).L) return
    const L = (window as any).L

    Object.values(markersRef.current).forEach((marker) => marker.remove())
    markersRef.current = {}

    objects.forEach((obj) => {
      if (!obj.coordinates) return

      const markerHTML = createMarkerHTML(obj, obj.id === selectedId, obj.id === hoveredId)
      const marker = L.marker([obj.coordinates.lat, obj.coordinates.lng], {
        icon: L.divIcon({
          className: "gidroatlas-marker-wrapper",
          html: markerHTML,
          iconSize: [40, 40],
          iconAnchor: [20, 40],
        }),
      })

      marker.on("click", (event: any) => {
        event.originalEvent?.stopPropagation()
        onSelect(obj)
      })

      marker.on("mouseover", () => onHover(obj.id))
      marker.on("mouseout", () => onHover(null))

      marker.addTo(mapRef.current)
      markersRef.current[obj.id] = marker
    })
  }, [objects, selectedId, hoveredId, onSelect, onHover])

  useEffect(() => {
    if (!mapRef.current || !onMapClick) return
    const handler = () => onMapClick()
    mapRef.current.on("click", handler)
    return () => {
      mapRef.current?.off("click", handler)
    }
  }, [onMapClick])

  const handleZoomIn = () => mapRef.current?.zoomIn()
  const handleZoomOut = () => mapRef.current?.zoomOut()

  return (
    <div className="w-full h-full relative bg-sky-100 rounded-3xl overflow-hidden">
      <div ref={mapContainerRef} className="absolute inset-0 z-0" />
      <div className="absolute inset-0 bg-gradient-to-b from-sky-50/20 via-transparent to-blue-50/30 pointer-events-none rounded-3xl" />

      <div className="absolute bottom-20 right-4 px-4 py-3 bg-white/95 backdrop-blur-sm rounded-2xl shadow-lg border border-gray-100 min-w-[220px]" onClick={(e) => e.stopPropagation()}>
        <div className="flex items-center gap-2 text-[11px] font-semibold text-gray-500 uppercase tracking-wide mb-1">
          <span className="w-1.5 h-1.5 rounded-full bg-emerald-500" />
          Легенда
        </div>
        <div className="flex flex-col gap-1.5">
          {[
            { c: 1, color: "bg-emerald-500", label: "в норме" },
            { c: 2, color: "bg-lime-500", label: "в норме" },
            { c: 3, color: "bg-amber-400", label: "монитор." },
            { c: 4, color: "bg-orange-500", label: "риск" },
            { c: 5, color: "bg-red-500", label: "критич." },
          ].map((item) => (
            <div key={item.c} className="flex items-center gap-2 text-[11px] text-gray-600">
              <span className={`w-6 h-6 rounded-full ${item.color} flex items-center justify-center text-[11px] text-white font-semibold shadow`}>
                {item.c}
              </span>
              <span>{item.label}</span>
            </div>
          ))}
        </div>
      </div>

      <div className="absolute top-24 left-6 flex flex-col gap-3 z-10" onClick={(e) => e.stopPropagation()}>
        <div className="relative">
          <button
            onClick={() => setShowMapMenu(!showMapMenu)}
            className="h-9 px-4 flex items-center justify-between gap-2 bg-white rounded-full shadow-md border border-gray-100 text-xs font-medium text-gray-700 hover:bg-gray-50 transition-colors min-w-[120px]"
          >
            Карта
            <ChevronDown className="w-3 h-3" />
          </button>
          {showMapMenu && (
            <div className="absolute bottom-full right-0 mb-2 w-36 bg-white border border-gray-100 rounded-2xl shadow-xl py-1">
              {[
                { value: "standard", label: "Стандарт" },
                { value: "satellite", label: "Спутник" },
                { value: "terrain", label: "Рельеф" },
              ].map((type) => (
                <button
                  key={type.value}
                  onClick={() => {
                    setMapType(type.value as typeof mapType)
                    setShowMapMenu(false)
                  }}
                  className={`w-full px-4 py-2 text-left text-xs hover:bg-gray-50 transition-colors ${
                    mapType === type.value ? "bg-blue-50 text-blue-700 font-semibold" : "text-gray-700"
                  }`}
                >
                  {type.label}
                </button>
              ))}
            </div>
          )}
        </div>

        <div className="flex flex-col bg-white rounded-2xl shadow-md border border-gray-100 overflow-hidden">
          <button onClick={handleZoomIn} className="w-10 h-10 flex items-center justify-center hover:bg-gray-50 border-b border-gray-100 transition-colors">
            <Plus className="w-4 h-4 text-gray-600" />
          </button>
          <button onClick={handleZoomOut} className="w-10 h-10 flex items-center justify-center hover:bg-gray-50 transition-colors">
            <Minus className="w-4 h-4 text-gray-600" />
          </button>
        </div>
      </div>
    </div>
  )
}
function createMarkerHTML(object: WaterObject, isSelected: boolean, isHovered: boolean) {
  const palette = {
    1: { bg: "#10b981", border: "#059669" },
    2: { bg: "#84cc16", border: "#4d7c0f" },
    3: { bg: "#f59e0b", border: "#b45309" },
    4: { bg: "#f97316", border: "#c2410c" },
    5: { bg: "#ef4444", border: "#b91c1c" },
  } as const

  const colors = palette[object.condition]
  const showLabel = isSelected || isHovered
  const needsTail = object.resourceType !== "reservoir"
  const isCritical = object.condition >= 4

  return `
    <div class="gidro-marker ${isSelected ? "gidro-marker--selected" : ""} ${
      isHovered ? "gidro-marker--hovered" : ""
    } gidro-marker--${object.resourceType}">
      ${showLabel ? `<span class="gidro-marker__label">${object.name}</span>` : ""}
      ${isCritical ? `<span class="gidro-marker__pulse" style="background:${colors.bg};"></span>` : ""}
      <span class="gidro-marker__bubble" style="background:${colors.bg};border-color:${colors.border};">
        <span class="gidro-marker__value">${object.condition}</span>
      </span>
      ${
        needsTail
          ? `<span class="gidro-marker__tail" style="border-top-color:${colors.bg};"></span>`
          : ""
      }
    </div>
  `
}
